                    secondOffset: `${offsetBase}%`,\n                                    thirdOffset: `${offsetBase + 100}%`,\n                                },\n\n                            redStop:\n                                {\n                                  color: `rgb(200, 0, 0)`,\n                                    opacity: (this.gradientOpacity.red ),\n                                  //= this.fluctuateValue(this.gradientOpacity.red, maxOpacity, minOpacity)\n                                    offset: `${offsetBase - 86}%`,\n                                    secondOffset: `${offsetBase + 14}%`\n                                },\n\n                            orangeStop:\n                                {\n\n                                    color: `rgb(100, 100, 0)`,\n                                    opacity: (this.gradientOpacity.orange ),\n                                  //= this.fluctuateValue(this.gradientOpacity.orange, maxOpacity, minOpacity)\n                                    offset: `${offsetBase - 72}%`,\n                                    secondOffset: `${offsetBase + 28}%`\n                                },\n\n                            yellowStop:\n                                {\n\n                                    color: `rgb(200, 200, 0)`,\n                                    opacity: (this.gradientOpacity.yellow ),\n                                  //= this.fluctuateValue(this.gradientOpacity.yellow, maxOpacity, minOpacity)\n                                    offset: `${offsetBase - 58}%`,\n                                    secondOffset: `${offsetBase + 42}%`\n                                },\n\n                            greenStop:\n                                {\n\n                                    color: `rgb(0, 200, 0)`,\n                                    opacity: (this.gradientOpacity.green ),\n                                  //= this.fluctuateValue(this.gradientOpacity.green, maxOpacity, minOpacity)\n                                    offset: `${offsetBase - 44}%`,\n                                    secondOffset: `${offsetBase + 56}%`\n                                },\n\n                            blueStop:\n                                {\n\n                                    color: `rgb(0, 100, 0)`,\n                                    opacity: (this.gradientOpacity.blue),\n                                  ///= this.fluctuateValue(this.gradientOpacity.blue, maxOpacity, minOpacity)\n                                    offset: `${offsetBase - 30}%`,\n                                    secondOffset: `${offsetBase + 70}%`\n                                },\n\n                            indigoStop:\n                                {\n\n                                    color: `rgb(0, 200, 100)`,\n                                    opacity: (this.gradientOpacity.indigo ),\n                                  //= this.fluctuateValue(this.gradientOpacity.indigo, maxOpacity, minOpacity)\n                                    offset: `${offsetBase - 16}%`,\n                                    secondOffset: `${offsetBase + 84}%`\n                                }\n\n                        }\n                };\n            },\n            mainLayerBackgroundOpacity: function () {\n                return (this.AnimationCurrentTime * .2) + .8;\n            }\n\n        },\n        created: function () {\n        //  this.animateSkyGradient()\n        },\n        methods: {\n            fluctuateValue: function (currentValue, maxValue, minValue) {\n\n                if (currentValue === undefined) {\n                    currentValue = minValue;\n                }\n                let opacityOffset = Math.floor(Math.random() * .2) + .009;\n\n                if (currentValue <= maxValue && currentValue >= minValue) {\n                    opacityOffset *= Math.floor(Math.random() * 2) == 1 ? 1 : -1;\n                } else if (currentValue >= maxValue) {\n                    opacityOffset *= -1;\n                }\n\n                return currentValue + opacityOffset;\n            },\n            animateSkyGradient: function () {\n\n            if (typeof this.nextAnimation === 'undefined' && typeof this.nextAnimation !== 'function'){\n              console.log(\"Animate: Creating Animation!\")\n\n              this.nextAnimation = function () {\n                this.AnimationCurrentTime += .0001;\n\n                if (this.skyGradientAnimationPlaying == true) {\n                  if (this.AnimationCurrentTime <= .99) {\n                    window.setTimeout(this.nextAnimation, 20);\n                  } else {\n                    this.AnimationCurrentTime = 0;\n                    window.setTimeout(this.nextAnimation, 20);\n                  }\n                } else {\n                  return;\n                }\n\n\n              }.bind(this);\n            }else if( typeof this.nextAnimation === 'function')\n            {\n              console.log(\"Animate: toggle Animation!\")\n              if (!this.skyGradientAnimationPlaying) {\n                this.skyGradientAnimationPlaying = true;\n                console.log(\"Animate: toggle Animation on!\")\n                this.nextAnimation()\n              } else {\n                this.skyGradientAnimationPlaying = false;\n                console.log(\"Animate: toggle Animation off!\")\n\n              }\n            }else\n            {\n              console.log(\"This is unexpected!\")\n            }\n\n\n\n\n            //make this.nextanimation\n\n\n\n           // nextAnimation();\n\n\n          },\n            loginLinkClicked: function () {\n                location.href = \"https://www.conspiron.com/logins/\";\n            },\n            getValueTransition: function (beginValue, endValue, amount) {\n                return beginValue + ((endValue - beginValue) * amount);\n            }\n        },\n        //Animation Control Functions\n        beginAnimation: function(){\n\n        },\n        mounted: function () {\n            this.animateSkyGradient();\n        }\n    };\n</script>\n<style>\n    .MainLayer {\n        position: absolute;\n        top: 0;\n        left: 0;\n\n        background: none;\n        color: white;\n        width: 100%;\n        transform: perspective(0);\n        text-align: center\n\n    }\n\n    #skyBackgroundStatic {\n      fill: url(#skyBackgroundStaticGradient);\n    }\n\n    #skyBackground {\n      fill: url(#logoPathCGradientFill);\n      stroke-width: 2px;\n      color: white;\n      stroke: black;\n      cursor: pointer;\n\n\n    }\n\n    #skyBlackBackground{\n      fill: #12123f;\n      stroke: white;\n      stroke-width: 2px;\n      background-color: white;\n      color: white;\n\n    }\n    #logoPathC {\n        fill: url(#logoPathCGradientFill);\n        stroke-width: 2px;\n        color: white;\n        stroke: black;\n        cursor: pointer;\n\n\n    }\n\n    #logoPathC:hover {\n        filter: url(#blurFilter);\n    }\n\n    #logoPathSwitch {\n        fill: black;\n        stroke-width: 2px;\n        color: white;\n        cursor: pointer;\n        stroke: black;\n\n    }\n\n    #logoPathSwitchUnder {\n        fill: rgba(0, 0, 0, 0.96);\n        stroke-width: 2px;\n        color: black;\n        cursor: pointer;\n        stroke: url(#logoPathCGradientFill);\n        filter: url(#blurFilter);\n    }\n\n    #logoPathSwitch:hover {\n        mask: url(\"#logoPathSwitchMask\");\n        fill: url(#logoPathCGradientFill);\n    }\n\n    .mainLayerIntro-enter-active, .mainLayerIntro-leave-active {\n        transition: opacity 3s ease-in;\n    }\n\n    .mainLayerIntro-enter, .mainLayerIntro-leave-to {\n        opacity: 0;\n    }\n</style>"],"sourceRoot":""}]);
